::-webkit-search-decoration {
  -webkit-appearance: none;
}

/*
1. Correct the inability to style clickable types in iOS and Safari.
2. Change font properties to `inherit` in Safari.
*/
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

:where(*),
:where(*)::before,
:where(*)::after {
  box-sizing: border-box;
}

/*
Add the correct display in Chrome and Safari.
*/
summary {
  display: list-item;
}

/*
Removes the default spacing for appropriate elements.
*/
blockquote,
dl,
dd,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
figure,
p,
pre {
  margin: 0;
}

fieldset {
  margin: 0;
  padding: 0;
}

legend {
  padding: 0;
}

ol,
ul,
menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

dialog {
  padding: 0;
}

/*
Prevent resizing textareas horizontally by default.
*/
textarea {
  resize: vertical;
}

/*
1. Reset the default placeholder opacity in Firefox. (https://github.com/tailwindlabs/tailwindcss/issues/3300)
2. Set the default placeholder color to the user's configured gray 400 color.
*/
input::placeholder,
textarea::placeholder {
  opacity: 1; /* 1 */
  color: theme("colors.gray.400", #9ca3af); /* 2 */
}

/*
Set the default cursor for buttons.
*/
button,
[role=button] {
  cursor: pointer;
  font-family: inherit;
}

/*
Make sure disabled buttons don't get the pointer cursor.
*/
:disabled {
  cursor: default;
}

/*
1. Make replaced elements `display: block` by default. (https://github.com/mozdevs/cssremedy/issues/14)
2. Add `vertical-align: middle` to align replaced elements more sensibly by default. (https://github.com/jensimmons/cssremedy/issues/14#issuecomment-634934210)
   This can trigger a poorly considered lint error in some tools but is included by design.
*/
img,
svg,
video,
canvas,
audio,
iframe,
embed,
object {
  display: inline-block; /* 1 */
  vertical-align: middle; /* 2 */
}

/*
Constrain images and videos to the parent width and preserve their intrinsic aspect ratio. (https://github.com/mozdevs/cssremedy/issues/14)
*/
img,
video {
  max-width: 100%;
  height: auto;
}

/*
Make elements with the HTML hidden attribute stay hidden by default.
*/
[hidden] {
  display: none;
}

/**
* helpers
**/
/**
* font
**/
body {
  --lb--caption-color-text: var(--lb-color-text-default);
}

.wp-caption {
  max-width: 100%;
  padding: 1.45em 0 2.9em;
}

.alignleft {
  float: left;
  margin-inline-end: 2em;
  text-align: left;
}

.alignright {
  float: right;
  text-align: right;
  margin-inline-start: 2em;
}

.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.alignfull {
  margin-left: 0;
  margin-right: 0;
  width: auto;
  max-width: 100vw;
}

.alignwide {
  margin-left: 0;
  margin-right: 0;
  width: auto;
  max-width: 100vw;
}

.wp-caption.alignleft,
.wp-caption.alignright,
.wp-caption.aligncenter {
  padding-bottom: 2.9em;
}

.wp-caption p.wp-caption-text {
  padding: 0;
  margin-bottom: 0;
  font-style: italic;
  font-size: var(--lb-font-size-text-xs-relative);
  line-height: var(--lb-line-height-text-xs-relative);
  letter-spacing: var(--lb-letter-spacing-text-xs-relative, inherit);
}

.wp-caption.alignleft p.wp-caption-text {
  text-align: left;
}

.wp-caption.aligncenter p.wp-caption-text {
  text-align: center;
}

.wp-caption p.wp-caption-text a:hover {
  text-decoration: underline;
}

ul.wp-block-gallery.alignfull,
ul.wp-block-gallery.alignwide {
  margin-left: -8px !important;
  margin-right: -8px !important;
  width: auto !important;
  max-width: 100vw !important;
  margin-top: 1.45em !important;
  margin-bottom: 2.9em !important;
}

.alignwide img,
.alignfull img {
  display: block;
  margin: 0 auto;
}

.wp-block-image figcaption {
  padding: 0;
  margin-top: var(--wp--preset--spacing--55);
  margin-bottom: 0;
  text-align: center;
  color: var(--lb--caption-color-text);
  font-size: var(--lb-font-size-text-xs-relative);
  line-height: var(--lb-line-height-text-xs-relative);
  letter-spacing: var(--lb-letter-spacing-text-xs-relative, inherit);
}

.wp-block-image.aligncenter {
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 0;
}

.wp-block-image.aligncenter > figcaption,
.wp-block-image.alignleft > figcaption,
.wp-block-image.alignright > figcaption,
.wp-block-image.is-resized > figcaption {
  display: block;
}

.wp-block-gallery .blocks-gallery-image,
.wp-block-gallery .blocks-gallery-item {
  margin: 0;
}

.wp-block-cover.alignleft,
.wp-block-cover-image.alignleft {
  float: left;
  margin: 8px 30px 20px 0;
  padding: 0 30px;
}

.wp-block-cover.alignright,
.wp-block-cover-image.alignright {
  float: right;
  margin: 8px 0 20px 30px;
  padding: 0 30px;
}

/*themes*/
/**
* itertors
**/
body {
  font-family: var(--wp--preset--font-family--base);
  --lb-ui-color-text: var(--lb-color-text-default);
  --lb-ui-color-heading-text: var(--lb-color-text-strong);
  --lb-ui-color-link-text: var(--lb-color-action-text-default);
}

.prose-headings :is(:where(h2):not(:where([class~=not-prose], [class~=not-prose] *))) {
  font-size: var(--lb-font-size-display-sm-relative);
  line-height: var(--lb-line-height-display-sm-relative);
  letter-spacing: var(--lb-letter-spacing-display-sm-relative, inherit);
  font-weight: 600;
}

.prose-headings :is(:where(h3):not(:where([class~=not-prose], [class~=not-prose] *))) {
  font-size: var(--lb-font-size-display-xs-relative);
  line-height: var(--lb-line-height-display-xs-relative);
  letter-spacing: var(--lb-letter-spacing-display-xs-relative, inherit);
  font-weight: 600;
}

.prose-content :is(:where(p):not(:where([class~=not-prose], [class~=not-prose] *))) {
  font-size: var(--lb-font-size-text-md-relative);
  line-height: var(--lb-line-height-text-md-relative);
  letter-spacing: var(--lb-letter-spacing-text-md-relative, inherit);
}

.prose-content :is(:where(ul, ol):not(:where([class~=not-prose], [class~=not-prose] *))) {
  font-size: var(--lb-font-size-text-md-relative);
  line-height: var(--lb-line-height-text-md-relative);
  letter-spacing: var(--lb-letter-spacing-text-md-relative, inherit);
  display: flex;
  flex-direction: column;
  row-gap: var(--wp--preset--spacing--90);
  padding-left: 0;
  list-style: none;
}
.prose-content :is(:where(ul, ol):not(:where([class~=not-prose], [class~=not-prose] *))) li::marker, .prose-content :is(:where(ul, ol):not(:where([class~=not-prose], [class~=not-prose] *))) li::before {
  color: var(--wp-list-ico-color-text);
}

.prose-content :is(:where(ul:not(.is-list-ico-check, .is-list-ico-cross)):not(:where([class~=not-prose], [class~=not-prose] *))) li {
  padding-left: var(--wp-list-ico-spacing-s);
  position: relative;
}
.prose-content :is(:where(ul:not(.is-list-ico-check, .is-list-ico-cross)):not(:where([class~=not-prose], [class~=not-prose] *))) li:before {
  content: "•";
  position: absolute;
  font-size: 1.75em;
  right: calc(100% - (var(--wp-list-ico-spacing-s) - 0.75em) / 2);
  top: 0;
}

.prose-content :is(:where(ol:not(.is-list-ico-check, .is-list-ico-cross)):not(:where([class~=not-prose], [class~=not-prose] *))) li {
  padding-left: var(--wp-list-ico-spacing-s);
  position: relative;
}
.prose-content :is(:where(ol:not(.is-list-ico-check, .is-list-ico-cross)):not(:where([class~=not-prose], [class~=not-prose] *))) li:before {
  content: counter(list-item);
  font-weight: 500;
  position: absolute;
  right: calc(100% - var(--wp-list-ico-spacing-s) / 2 + 1px);
  top: 0;
}

.prose-content :is(:where(img):not(:where([class~=not-prose], [class~=not-prose] *))) {
  width: 100%;
}

.prose-spaces :is(:where(h1, h2, h3, h4, h5, h6, p, ul, ol, blockquote, .wp-block-image, .wp-block-columns, .lb-group-list, .lb-casino-details, .lb-casino-list, .lb-author-block, [id*=sp_easy_accordion]):not(:where([class~=not-prose], [class~=not-prose] *))):first-child {
  margin-block-start: 0;
}
.prose-spaces :is(:where(h1, h2, h3, h4, h5, h6, p, ul, ol, blockquote, .wp-block-image, .wp-block-columns, .lb-group-list, .lb-casino-details, .lb-casino-list, .lb-author-block, [id*=sp_easy_accordion]):not(:where([class~=not-prose], [class~=not-prose] *))):last-child {
  margin-block-end: 0;
}

.prose-spaces :is(:where(.wp-block-columns, .lb-group-list):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--100);
  margin-block-end: var(--wp--preset--spacing--100);
}

.prose-spaces :is(:where(h2):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--160);
  margin-block-end: var(--wp--preset--spacing--100);
}

.prose-spaces :is(:where(h3):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--160);
  margin-block-end: var(--wp--preset--spacing--80);
}
@media (max-width: 767.98px) {
  .prose-spaces :is(:where(h3):not(:where([class~=not-prose], [class~=not-prose] *))) {
    margin-block-start: var(--wp--preset--spacing--140);
  }
}

.prose-spaces :is(:where(p, .wp-block-image):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--80);
  margin-block-end: var(--wp--preset--spacing--80);
}

.prose-spaces :is(:where(ul, ol):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--90);
  margin-block-end: var(--wp--preset--spacing--90);
}

.prose-spaces :is(:where(blockquote):not(:where([class~=not-prose], [class~=not-prose] *))) {
  margin-block-start: var(--wp--preset--spacing--100);
